.projectcard_container{

}
.projectcard {
    position: relative;
    width: 350px;
    aspect-ratio: 16/9;
    background-color: #f2f2f2;
    border-radius: 10px;
    display: flex;
    align-items: center;
    justify-content: center;
    overflow: hidden;
    perspective: 1000px;
    box-shadow: 0 0 0 5px #ffffff80;
    transition: all 0.6s cubic-bezier(0.175, 0.885, 0.32, 1.275);
  }
  
  .projectcard img {
  max-width: 100%; /* Set the maximum width of the image to 100% of its container */
  max-height: 100%; /* Set the maximum height of the image to 100% of its container */
    fill: #333;
    transition: all 0.6s cubic-bezier(0.175, 0.885, 0.32, 1.275);
}

  
  .projectcard:hover {
    transform: scale(1.05);
    box-shadow: 0 8px 16px rgba(255, 255, 255, 0.2);
  }
  
  .projectcard__content {
    gap: 1rem;
    position: absolute;
    top: 0;
    left: 0;
    width: 100%;
    height: 100%;
    padding: 20px;
    box-sizing: border-box;
    background-color: #f2f2f2;
    transform: rotateX(-90deg);
    transform-origin: bottom;
    transition: all 0.6s cubic-bezier(0.175, 0.885, 0.32, 1.275);
 
}
  
  .projectcard:hover .projectcard__content {
    transform: rotateX(0deg);
  }
  
  .projectcard__title {
    margin: 0;
    font-size: 20px;
    color: #333;
    font-weight: 700;
  }
  
  .projectcard:hover svg {
    scale: 0;
  }
  
  .projectcard__description {
    margin: 10px 0 10px;
    font-size: 12px;
    color: #777;
    line-height: 1.4;
  }
  
  .projectcard__button {
    padding: 15px;
    border-radius: 8px;
    background: #777;
    border: none;
    color: white;
    cursor: pointer;
  }
  
  .secondary {
    background: transparent;
    color: #777;
    border: 1px solid #777;
    cursor: pointer;
  }
  

